/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - begin
   ========================================================================== */

@media only screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape) {
    .mkdf-woo-single-page {
        .woocommerce-tabs {
            ul.tabs {
                > li {
                    a {
                        padding: 12px 7px;
                    }
                }
            }
        }
    }
}

@include ipad-landscape {
    .mkdf-woo-single-page {
        .mkdf-single-product-content {
            .images,
            .mkdf-single-product-summary {
                float: none;
                padding: 0;
                width: 100%;
            }

            .images {
                .woocommerce-product-gallery__trigger {
                    right: 10px;
                }

                .woocommerce-product-gallery__image {
                    &:not(:first-child) {
                        box-sizing: border-box;
                        display: inline-block;
                        float: left;
                        margin: 4% 2% 0 2%;
                        width: calc(92% / 3);
                    }

                    &:nth-child(3n+2) {
                        margin-left: 0;
                    }

                    &:nth-child(3n+4) {
                        margin-right: 0;
                    }
                }
            }

            .mkdf-single-product-summary {
                margin: 30px 0 0;
            }
        }

        .woocommerce-tabs {
            ul.tabs {
                > li {
                    a {
                        padding: 13px 10px;
                    }
                }
            }
        }
    }
}

@include phone-landscape {
    .mkdf-woo-single-page {
        .mkdf-single-product-content {
            .images {
                .woocommerce-product-gallery__image {
                    &:not(:first-child) {
                        width: calc(96% / 2);
                    }

                    &:nth-child(3n+2) {
                        margin-left: 2%;
                    }

                    &:nth-child(3n+4) {
                        margin-right: 2%;
                    }

                    &:nth-child(2n+2) {
                        margin-left: 0;
                    }

                    &:nth-child(2n+3) {
                        margin-right: 0;
                    }
                }
            }
        }

        .woocommerce-tabs ul.tabs > li {
            display: block;
            margin-right: 0;
        }
    }
}

/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - end
   ========================================================================== */